@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base customizations */
:root {
  --color-highlight: #00ddffde; /* Light teal for accents */
  --color-primary: #f7fafc; /* Light background color */
  --color-secondary: #e2e8f0; /* Lighter gray background */
  --color-accent: #edf2f7; /* Soft white color */
}

/* Body and general layout */
body {
  font-family: 'Inter', sans-serif;
  background: var(--color-primary);
  color: var(--color-accent);
  overflow-x: hidden;
  margin: 0;
}

/* Hero Section */
.hero-background {
  position: relative; /* To position the bubbles behind content */
  z-index: 0; /* Keep the hero content on top of the bubbles */
}

/* Bubbles container */
.bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Prevents the bubbles from blocking interactions */
  z-index: -1; /* Keeps the bubbles behind the content */
}

/* Bubble Style */
.bubble {
  position: absolute;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2); /* Light color */
  animation: bubbleAnimation 10s infinite ease-in-out;
}

/* Randomized animations */
@keyframes bubbleAnimation {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0.2;
    width: 50px;
    height: 50px;
  }
  25% {
    transform: translateY(-30px) translateX(40px);
    opacity: 0.25;
    width: 60px;
    height: 60px;
  }
  50% {
    transform: translateY(-60px) translateX(80px);
    opacity: 0.3;
    width: 70px;
    height: 70px;
  }
  75% {
    transform: translateY(-90px) translateX(120px);
    opacity: 0.35;
    width: 80px;
    height: 80px;
  }
  100% {
    transform: translateY(0) translateX(0);
    opacity: 0.2;
    width: 50px;
    height: 50px;
  }
}

/* Bubble 1: Position and animation delay */
.bubble-1 {
  top: 10%;
  left: 30%;
  animation-delay: 0s;
}

/* Bubble 2: Position and animation delay */
.bubble-2 {
  top: 20%;
  left: 50%;
  animation-delay: 1s;
}

/* Bubble 3: Position and animation delay */
.bubble-3 {
  top: 40%;
  left: 70%;
  animation-delay: 2s;
}

/* Bubble 4: Position and animation delay */
.bubble-4 {
  top: 60%;
  left: 20%;
  animation-delay: 3s;
}

/* Bubble 5: Position and animation delay */
.bubble-5 {
  top: 80%;
  left: 60%;
  animation-delay: 4s;
}

