/* Apply background to all sections */
section {
  position: relative;
  overflow: hidden;
  z-index: 1;
}

/* Static Background for Hero Section and all sections */
.hero-background,
section {
  background: linear-gradient(45deg, #386a6000, #2c5c86c3); /* Light teal to light gray gradient */
  position: relative;
  z-index: 1;
  background-size: cover; /* Ensure the background size covers the entire section */
  background-attachment: fixed; /* Prevents the background from scrolling */
  will-change: background-position; /* Optimize performance */
}

/* Navigation Button Styles */
.nav-btn {
  font-size: 1rem;
  font-weight: 600;
  color: #edf2f7;
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  background-color: transparent;
  border: 2px solid transparent;
  z-index: 10;
}

.nav-btn:hover {
  background-color: #4CAF50;
  color: white;
  transform: translateY(-2px);
}

.nav-btn:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(72, 202, 228, 0.6);
}

/* Adjust spacing for GitHub and LinkedIn links */
.links-container {
  display: flex; /* Align links horizontally */
  justify-content: center; /* Center align them */
  gap: 1rem; /* Space between the links */
  margin: 0; /* Remove unnecessary margins */
  padding: 0; /* Remove unnecessary padding */
}

a {
  text-decoration: none; /* Remove underline */
  margin: 0;
  padding: 0;
}

/* Animations */
.fade-in-up {
  animation: fadeInUp 1s ease-out forwards;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-in {
  animation: slideIn 1s ease-out forwards;
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Custom Heading Styles */
h1, h2, h3 {
  font-family: 'Lora', serif; /* Elegant serif font for headings */
  font-weight: 500;
  font-style: italic;
  text-align: center;
  color: var(--color-highlight);
}

h1 {
  font-size: 3rem; /* Larger size for main heading */
}

h2 {
  font-size: 2.5rem; /* Slightly smaller for sub-headings */
}

h3 {
  font-size: 2rem; /* Medium for section titles */
}

h2.fade-in, h3.fade-in, h1.fade-in {
  animation: fadeInLeft 1s ease-out forwards;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Emphasize Web Developer Intern text */
.web-developer-intern {
  font-size: 1.25rem; /* Increased font size for prominence */
  font-weight: bold; /* Make it bold */
  color: #e2e8f0; /* Accent color (light teal or green) */
  text-align: left; /* Align left for better readability in Work Experience */
  letter-spacing: 0.5px; /* Slightly space out the letters */
  margin-top: 1rem;
  text-transform: uppercase; /* Uppercase text for extra emphasis */
  font-family: 'Inter', sans-serif; /* Consistency with the font used in the portfolio */
}

/* Text Styles for Work Experience, About Me, Projects */
.text-accent {
  font-family: 'Lora', serif; /* Elegant font for paragraphs */
  color: #edf2f7; /* Light text color */
  font-weight: 400;
  letter-spacing: 0.5px; /* Improve readability */
}

.text-md {
  font-size: 1.25rem; /* Slightly larger text for better readability */
  color: #d1d5db; /* Softer grey for less harsh contrast */
}

.text-lg {
  font-size: 1.5rem; /* Larger text for key information */
  color: #e2e8f0; /* Lighter color for better contrast */
}

.text-xl {
  font-size: 1.75rem; /* Large text for important sections */
}

/* Paragraph text styling in various sections */
section p, section ul {
  font-size: 1.1rem; /* Default text size for paragraphs and lists */
  color: #cbd5e0; /* Softer grey for better readability */
  line-height: 1.8; /* Increased line-height for readability */
}

ul.list-disc li {
  font-size: 1.25rem;
  color: #cbd5e0; /* Softer grey */
  line-height: 1.6;
}

ul.list-disc li:hover {
  color: #00b894; /* Change color on hover for interaction */
  cursor: pointer;
}

/* Adding a highlight for clickable phone number and email */
.text-highlight {
  color: #4fffdc; /* Light teal for emphasis */
  text-decoration: none; /* Remove underline by default */
  transition: color 0.3s ease-in-out; /* Smooth color transition */
}

.text-highlight:hover {
  color: #00b894; /* Slightly darker teal on hover */
  text-decoration: underline; /* Underline on hover for interactivity */
}
